/* Below 1000px */
@media (max-width: 62.5em) {
  .hero-buttons .btn-login,
  .hero-buttons .btn-demo-login {
    width: 25%;
  }
  .hero-title,
  .hero-secondary {
    width: 50%;
  }
}
/* Below 500px */
@media (max-width: 43.75em) {
  .hero-title,
  .hero-secondary {
    width: 90%;
  }
  .hero-title {
    font-size: 2.25rem;
  }
  .hero-secondary {
    font-size: 1.1rem;
  }
  .hero-buttons .btn-login,
  .hero-buttons .btn-demo-login {
    width: 50%;
  }
  .form-background {
    width: 80%;
    margin: 0 auto;
  }
  .logo {
    width: 40%;
  }
  .navbar {
    padding: 0.7rem;
  }
  .dashboard-wrapper {
    padding: 1rem;
  }

  .modal-open .dashboard-wrapper {
    padding: 0 0.5rem;
  }

  .dashboard-header {
    padding: 0.8rem 1rem;
  }
  .features-section {
    margin-top: 4rem;
  }
  .feature:nth-child(even) {
    flex-direction: column-reverse;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1140px;
  }
}
